@import url(https://fonts.googleapis.com/css?family=Kalam&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:200,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:200,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (max-width: 500px) {
  .home {
    padding: 100px 40px;
  }
}

.layout {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.layout-children {
  flex: 2 1;
  padding: 100px;
}

@media (max-width: 500px) {
  .layout {
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  .layout-children {
    width: 80vw;
    /* flex: 4; */
    padding: 0px;
    margin: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

nav {
  flex: 1 1;
  position: fixed;
  width: 100%;
  background-color: white;
  z-index: 1;
  top: 0;
  left: 0;
}

.nav {
  display: flex;
  padding: 20px 0;
  margin-left: -2px;
  margin-right: -2px;
  margin-top: -2px;
  box-shadow: 1px 1px 1px 0.3px #cecece;
  width: 100%;
}

.logo {
  font-family: 'Kalam', cursive;
  text-align: center;
  font-weight: bold;
  font-size: 34px;
  letter-spacing: -3px;
  flex: 1 1;
  max-width: 30vw;
  transition: 250ms all;
}

.logo:hover {
  transform: scale(1.004);
}

.links {
  flex: 2 1;
  max-width: 60vw;
  align-items: center;
  font-size: 18px;
  display: flex;
}

.link {
  flex: 1 1;
}

.welcome {
  padding: 0 100px;
}

a {
  text-decoration: none;
  color: black;
}

@media (max-width: 500px) {
  nav {
    width: 98%;
  }
  .logo {
    max-width: 50%;
  }
  .links {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 50vw;
  }
  .link {
    padding: 3px;
  }
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1;
  padding: 20px;
}

footer a {
  text-decoration: underline;
}

.products {
    width: 90vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding: 40px;
}

.sort-container {
    display: flex;
    width: 83.5vw;
    justify-content: flex-end;
    margin: 20px;
}

label {
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    padding-right: 10px;
}

select {
    cursor:pointer;
    border: none;
    font-size: 18px;
}

@media (max-width: 500px) {
    .products {
        width: 100%;
        justify-content: space-around;
    }
    .sort-container {
        width: 100%;
        justify-content: center;
        margin: 0px;
    }
}
.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
  box-shadow: 0px 0px 3px 1px #cecece;
  transition: 250ms all;
  padding: 20px;
  margin-bottom: 60px;
}

.product:hover {
  transform: scale(1.02);
  opacity: 0.9;
}

.product-image {
  width: 300px;
}

.product-name {
  font-size: 24px;
  padding: 10px;
}

.price {
  font-size: 18px;
  padding: 10px;
}

@media (max-width: 500px) {
  .product {
    width: 100%;
    margin-bottom: 20px;
  }
  .product-image {
    width: 180px;
    margin: 15px;
    padding: 10px;
  }
}

.search-form {
  padding-top: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.search-input {
  width: 70vw;
  font-size: 18px;
  letter-spacing: 0.1px;
  padding: 12px 20px 12px 40px;
  margin: 40px;
}

.search-input::-webkit-input-placeholder {
  letter-spacing: 0.3px;
}

.search-input:-ms-input-placeholder {
  letter-spacing: 0.3px;
}

.search-input::-ms-input-placeholder {
  letter-spacing: 0.3px;
}

.search-input::placeholder {
  letter-spacing: 0.3px;
}

.create-form {
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  justify-content: space-evenly;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

input,
textarea {
  font-size: 48px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  border-top-style: hidden;
  border-right-style: hidden;
  border-left-style: hidden;
  border-bottom-style: hidden;
}

textarea {
  font-size: 24px;
  font-family: Arial, Helvetica, sans-serif;
}

.input-name {
  font-size: 48px;
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.input-price {
  padding-left: 10px;
  font-size: 24px;
  font-family: Arial, Helvetica, sans-serif;
}

.input-image-link {
  font-size: 18px;
  font-family: Arial, Helvetica, sans-serif;
}

.submit-button {
  font-size: 22px;
  font-family: Arial, Helvetica, sans-serif;
  height: 40px;
  margin-top: 40px;
  background-color: black;
  color: white;
  border: none;
  box-shadow: 2px 2px 10px 2px #cecece;
  cursor: pointer;
}

@media (max-width: 500px) {
  .create-form {
    padding-top: 150px;
  }

  input,
  textarea {
    font-size: 28px;
  }
}

.product-edit {
  display: flex;
  padding: 100px;
  padding-top: 80px;
}

.image-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.edit-product-image {
  width: 500px;
  padding: 0 40px;
  padding-bottom: 5px;
}

.edit-input-image-link {
  width: 300px;
  text-align: center;
  font-size: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.edit-form {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  line-height: 1.6;
}

.input-name {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-size: 64px;
  letter-spacing: 1px;
  padding: 5px;
}

.textarea-description {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-size: 16px;
  padding: 10px;
  letter-spacing: 0.9px;
  line-height: 1.6;
}

.save-button {
  font-size: 22px;
  font-family: Arial, Helvetica, sans-serif;
  height: 40px;
  width: 200px;
  margin-top: 40px;
  background-color: black;
  color: white;
  border: none;
  box-shadow: 2px 2px 10px 2px #cecece;
  cursor: pointer;
}

@media (max-width: 500px) {
  .input-name {
    margin-left: -10px;
    padding-left: -10px;
    font-size: 26px;
  }
  .edit-form {
    padding: 0px;
  }
  .textarea-description {
    width: 100%;
    padding: 0px;
  }
  .product-edit {
    width: 100%;
    flex-direction: column;
  }
  .image-container {
    width: 100%;
  }
  .edit-product-image {
    width: 300px;
  }
}

.product-detail {
  display: flex;
  padding: 100px;
  padding-top: 80px;
}

.detail {
  display: flex;
  flex-direction: column;
  line-height: 1.6;
}

.name {
  font-size: 64px;
  letter-spacing: 1px;
  padding: 5px;
}

.price {
  font-size: 28px;
}

.description {
  padding: 10px;
  letter-spacing: 0.9px;
}

.product-detail-image {
  width: 500px;
  padding: 0 40px;
}

.button-container {
  display: flex;
  justify-content: space-evenly;
}

.edit-button,
.delete-button {
  font-size: 22px;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  height: 40px;
  width: 20%;
  align-self: center;
  margin-top: 40px;
  background-color: black;
  color: white;
  border: none;
  box-shadow: 2px 2px 10px 2px #cecece;
  cursor: pointer;
}

.edit-link {
  color: white;
}

@media (max-width: 1024px) {
  .name {
    font-size: 32px;
  }
  .product-detail-image {
    width: 500px;
    margin-top: -100px;
    padding: 10px;
  }
  .product-detail {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

@media (max-width: 800px) {
  .product-detail-image {
    width: 300px;
    margin: 15px;
    padding: 10px;
  }
  .product-detail {
    padding: 0px;
    padding-top: 120px;
    /* margin: 0px; */
    /* display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center; */
  }
  .button-container {
    /* display: flex;
        justify-content: space-evenly;
        padding-top: 0px; */
    padding: 20px;
    padding-bottom: 80px;
  }
  .edit-button,
  .delete-button {
    width: 40%;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat';
  overflow: hidden;
  height: 100%;
}

button {
  cursor: pointer;
  font-size: 1rem;
  padding: 0.6em 1em;
  border-radius: 3px;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
}

.navbar {
  background: #0097a7;
  height: 4em;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar .nav .links a,
.navbar span {
  margin: 0 1em;
  color: #f8f8f8;
  text-decoration: none;
  font-size: 1.1rem;
  transition: all 0.4s ease;
}

.navbar .nav .links a:hover {
  color: #ffccbc;
}

.navbar .nav {
  display: flex;
  transition: all 0.4s ease;
}

.container {
  height: 100%;
}

.row {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3em;
}
.form-container {
  align-self: center;
  padding: 1em;
  width: 50%;
  height: 80%;
  margin: 4em auto;
  border: 2px solid #b0bec5;
  border-radius: 6px;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.3);
  transition: all 2s ease;
}

.form-container .danger {
  background: #e57373;
}

.form-container h3 {
  text-align: center;
}

.form-container form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  width: 80%;
}

.form-container form input {
  width: 100%;
  padding: 0.6em 1em;
  margin: 0.5em auto;
  font-size: 1rem;
  border-radius: 3px;
  border: 2px solid #e0e0e0;
  outline: none;
  transition: all 0.4s ease;
}

.form-container form input:focus,
.form-container form input:active {
  border: 2px solid #82ada9;
}

.form-container form input:valid {
  border: 2px solid #66bb6a;
}

.form-container form button {
  width: 50%;
  margin: 1em auto;
  background: #56c8d8;
  border: none;
}

.form-container form button:hover {
  box-shadow: 3px 8px 16px rgba(0, 0, 0, 0.5);
}

.form-container form a {
  margin: auto;
}

.form-container form label {
  margin-top: 0.6em;
}

.layout {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  height: Calc(100vh - 4em);
}

.layout .content {
  display: flex;
  flex-direction: row;
  position: relative;
  flex: 1 1;
}

.layout .content nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 0.8 1;
  z-index: 1000;
  background: #f8f8f8;
  box-shadow: inset -10px 8px 10px -6px rgba(0, 0, 0, 0.3);
}

.layout .content nav a {
  text-decoration: none;
  text-align: center;
  padding: 1em 0;
  font-size: 1.1rem;
  width: 100%;
  color: #333;
  transition: all 0.4s ease;
}

.layout .content nav a:hover {
  background: #616161;
  color: #f8f8f8;
}

.layout .content nav a:first-of-type {
  border-bottom: 2px solid #333;
}

.layout .content nav a:last-of-type {
  border-bottom: 2px solid #333;
}

.layout .content .main {
  flex: 3 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.layout .content .main .item-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2em;
  grid-template-rows: auto;
  width: 80%;
  padding: 2em 0;
}

.item {
  border: 2px solid #81b9bf;
  text-align: center;
  padding: 1em;
  border-radius: 3px;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  min-width: 300px;
}

.item .buttons {
  margin-top: 1em;
  display: flex;
  justify-content: space-around;
}

.item h4 {
  font-size: 1.2rem;
}

.item a {
  text-decoration: none;
  text-align: center;
  padding: 1em 0;
  font-size: 1.1rem;
  color: #333;
  transition: all 0.4s ease;
  width: auto;
  align-self: flex-start;
}

.item a:hover {
  opacity: 0.5;
}

.item a::before {
  content: '<-';
}

.item-container .item {
  min-width: 200px;
  min-height: 100px;
}

.item-container .item button {
  background: #0097a7;
  color: #f8f8f8;
}

.item-container .item button:hover {
  background: #56c8d8;
  color: #333;
}

.danger {
  background: #e57373;
}

.error {
  text-align: center;
  color: #e57373;
  font-size: 1.1rem;
}

.edit {
  background: #b2dfdb;
}

.edit:hover {
  background: #82ada9;
}

.danger:hover {
  color: #f8f8f8;
  background: #af4448;
}

.alert {
  /* background: #ef9a9a; */
  width: 400px;
  height: 200px;
  border-radius: 3px;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: 20px;
  left: 50%;
  margin: 0 auto;
  transform: translateX(-50%);
}

.alert h3 {
  font-size: 1.2rem;
  font-weight: 800;
  align-self: flex-end;
  margin-top: 5px;
  margin-right: 0.5em;
  cursor: pointer;
  transition: all 0.4s ease;
}

.alert h3:hover {
  color: #f8f8f8;
}

.alert p {
  color: #388e3c;
  width: 80%;
  margin: auto;
  align-self: center;
  text-align: center;
  flex: 2 1;
}

.hide {
  transform: translate(-50%, 200%);
}

.show {
  transform: translate(-50%, -10%);
}

.success {
  background: #dcedc8;
}

.success h3:hover {
  color: #333;
  opacity: 0.6;
}

.landing .container {
  text-align: center;
}

.landing .main .item-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2em;
  grid-template-rows: auto;
  width: 80%;
  margin: 4em auto;
}

